import React from 'react';
import Drawer from 'material-ui/Drawer';
//import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
//import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import {cyan500} from 'material-ui/styles/colors';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import IconMenu from 'material-ui/IconMenu';
import Popover from 'material-ui/Popover';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
//import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import ImageCamera from 'material-ui/svg-icons/image/camera';
import ImagePhotoLibrary from 'material-ui/svg-icons/image/photo-library';
import NavigationMenu from 'material-ui/svg-icons/navigation/menu';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import PopCamera from './PopCameraIcon.jsx';
import injectTapEventPlugin from 'react-tap-event-plugin';
import FlatButton from 'material-ui/FlatButton';
import Dialog from 'material-ui/Dialog';


injectTapEventPlugin();

const muiTheme = getMuiTheme({
  palette: {
    textColor: cyan500,
  },
  appBar: {
    height: 50,
    color: cyan500
  },
});


class UserAppBar extends React.Component {


constructor(props) {
    super(props);
    this.state = {
      drawerOpen: false,
      loggedIn: false,
      loginDialogOpen :false

    };
    this.handleToggle = this.handleToggle.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleLoginDialogOpen = this.handleLoginDialogOpen.bind(this);
    this.handleLoginDialogClose = this.handleLoginDialogClose.bind(this);
  }
  handleLoginDialogOpen(){ this.setState({loginDialogOpen: true}); }

  handleLoginDialogClose(){ this.setState({loginDialogOpen: false}); }

  handleToggle(){ this.setState({drawerOpen: !this.state.drawerOpen}); }

  handleClose() {this.setState({drawerOpen: false}); }


  render() {

    const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        label="Submit"
        primary={true}
        keyboardFocused={true}
        onTouchTap={this.handleClose}
      />,
    ];

 var loginButton = (<div> <FlatButton label="Login" onTouchTap={this.handleLoginDialogOpen}/> 
        <Dialog
          title="Dialog With Actions"
          actions={actions}
          modal={false}
          open={this.state.open}
          onRequestClose={this.handleClose}
        >
          The actions in this window were passed in as an array of React objects.
        </Dialog>

 </div>);


    return(
  
<div>
<AppBar
    title="PhotoSpeaks"
    onLeftIconButtonTouchTap= {this.handleToggle}
  
    iconElementRight={ loggedIn ? <PopCamera /> : {loginButton}  }
  />
 <Drawer docked={false} 
       open={this.state.drawerOpen}
onRequestChange={(drawerOpen) => this.setState({drawerOpen})}
       >
          <MenuItem onTouchTap={this.handleClose}><NavigationClose /></MenuItem>
          <MenuItem >{this.props.username}</MenuItem>
        </Drawer>
        </div>
);
}
}


export default UserAppBar;


